import { Component, OnDestroy, OnInit } from '@angular/core';
import { TeamsummaryService } from 'src/app/teamsummary.service';
import { Team } from 'src/app/team';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-teamsummary',
  templateUrl: './teamsummary.component.html',
  styleUrls: ['./teamsummary.component.scss']
})
export class TeamsummaryComponent implements OnInit   {
  teamMembersSummary:Team[];

  constructor(private teamSummaryService:TeamsummaryService) {  }

  ngOnInit(){
    this.teamSummaryService.getTeamSummary().subscribe(
      (res:Team[])=>{
        this.teamMembersSummary=res;
      }
    );
    
  }
   
}

 